
import React from 'react';
import { useHistory } from 'react-router-dom';
import '../App.css';

function AddCity() {

const history = useHistory();

var city = '';
var state = '';

function handleCity(e) {
city= e.target.value;
}

function handleState(e) {
state= e.target.value;
}



function handleClickCancel() {
history.push("/");
}

function handleAddCity() {
localStorage.setItem('CityAndState', city + ' ' + state);
history.push("/");
}


return(
<div className="container-fluid h-custom">
<div className="row d-flex justify-content-center align-items-center h-100">
<div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
<form >
<div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '10vh'}}>
<h1>Add City</h1>
</div>

<div className="divider d-flex align-items-center my-4">
<p className="text-center fw-bold mx-3 mb-5"></p>
</div>
<div className="form-outline mt-4 mb-4">
<input type="text"
className="form-control form-control-lg"
placeholder="Only enter US city. Ex: Orlando"
onChange={handleCity}
/>
<label className="form-label">Enter City</label>
</div>
<div className="form-outline mb-3">
<input type="text"
className="form-control form-control-lg"
placeholder="Only enter US state. Ex: Florida"
onChange={handleState}
/>
<label className="form-label">Enter State</label>
</div>

<div className="form-group row d-flex justify-content-center ">
<div className="col-md-4">
<button type="button" className="btn btn-secondary btn-lg form-control" onClick={handleAddCity}>Add</button>
</div>

<div className="col-md-4">
<button type="button" className="btn btn-dark btn-lg form-control" onClick={handleClickCancel}>Cancel</button>
</div>

<div className="divider d-flex align-items-center my-4">
<p className="text-center fw-bold mx-3 mb-5"></p>
</div>
</div>

</form>
</div>
</div>
</div>

);
};

export default AddCity;
