
import React from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import { DropdownButton } from 'react-bootstrap';
import { Dropdown } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';


function TopBorder()
{
const history = useHistory();

var userFullName = '';
var buttonLabel = 'Login / Register';
var validUsr = false;
if ( localStorage.accessToken !== '' ) {
userFullName = "Welcome: " + localStorage.userFirstName + ' ' + localStorage.userLastName;
validUsr = true;
buttonLabel = 'Logout'
}

const [show, toggle] = React.useState(validUsr);
const ToggleShow = () => toggle(validUsr);


function handleClick() {
if ( localStorage.accessToken === '' ) {
history.push("/login");
} else {
userFullName = '';
localStorage.setItem('accessToken', '');
localStorage.setItem('userId', '');
localStorage.setItem('userFirstName', '');
localStorage.setItem('userLastName', '');
localStorage.setItem('userName', '');
localStorage.setItem('userPassword', '');
window.location.reload(false);
}
}

function handleClickCity () {
history.push("AddCity")
}

function handleClickSettings() {
history.push("/Settings")
}

// const Toggle = () => {
//   const [show, toggleShow] = React.useState(validUsr);
// }

return(
<div className="input-group mt-1 mb-2">

{show &&
<div>
<DropdownButton className="input-group-append icon-play" id="dropdown-basic-button" size="sm" variant="Secondary" menuVariant="secondary">
<Dropdown.Item href="#/action-1" onClick={handleClickCity}>Add City</Dropdown.Item>
</DropdownButton>
</div>
}


<input className="form-control text-left fw-bold" placeholder={userFullName} />

<div className="input-group-append" >
<button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={handleClick}>{buttonLabel}</button>
</div>


{show &&
<div className="input-group-append">
<button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={handleClickSettings}>Settings</button>
</div>
}

</div>

);
};

export default TopBorder;

