import React, { Component } from 'react';
import axios from 'axios';
import { Typeahead } from 'react-bootstrap-typeahead';
// import ReactDOM from 'react-dom';
import '../App.css';
import options from './data';
import 'react-bootstrap-typeahead/css/Typeahead.css';


function filterBy(option, state) {
if (state.selected.length) {
return true;
}
return option.label.toLowerCase().indexOf(state.text.toLowerCase()) > -1;
}

const ToggleButton = ({ isOpen, onClick }) => (
<button
className="toggle-button"
onClick={onClick}
onMouseDown={e => {
e.preventDefault();
}}>
{isOpen ? '▲' : '▼'}
</button>
);

export default class searchCity extends Component {

constructor(props) {
super(props);

this.state = {
cities: [],
users: []
};
}

componentDidMount() {

axios.get('https://rate-my-city.herokuapp.com/listCities')
// axios.get('https://jsonplaceholder.typicode.com/users')
.then(response => {
if (response.data.length > 0) {
this.setState({
cities: response.data.map(({ name: label }) => ({ label }))
// cities: response.data.map(({ username: label }) => ({ label }))
})
}
})
.catch(function (error) {
console.log(error);
}
);

axios.get('https://rate-my-city.herokuapp.com/listUsers')
// axios.get('https://jsonplaceholder.typicode.com/users')
.then(response => {
if (response.data.length > 0) {
this.setState({
users: response.data.map(({ userName: label }) => ({ label }))
// users: response.data.map(({ name: label }) => ({ label }))
})
}
})
.catch(function (error) {
console.log(error);
}
);

}

onSubmit(e) {
e.preventDefault();

this.setState ({
cityName: '',
userName: ''
})
}

render () {
return (

<div className="input-group d-flex justify-content-center">

<div className="form-group">
<Typeahead
filterBy={filterBy}
options={this.state.cities}
placeholder="Search by City ...">
{({ isMenuShown, toggleMenu }) => (
<ToggleButton isOpen={isMenuShown} onClick={e => toggleMenu()} />
)}
</Typeahead>
</div>

<div className="form-group">
<Typeahead
filterBy={filterBy}
options={options}
placeholder="Search by state ...">
{({ isMenuShown, toggleMenu }) => (
<ToggleButton isOpen={isMenuShown} onClick={e => toggleMenu()} />
)}
</Typeahead>
</div>

<div className="form-group">
<Typeahead
filterBy={filterBy}
options={this.state.cities}
placeholder="Search by User ...">
{({ isMenuShown, toggleMenu }) => (
<ToggleButton isOpen={isMenuShown} onClick={e => toggleMenu()} />
)}
</Typeahead>
</div>

<div>
<button className="btn text-white fw-bold btn-outline-dark btn-secondary" >Search</button>
</div>

<div>
<p>&nbsp;</p>
<p>&nbsp;</p>
<p>&nbsp;</p>
<p>&nbsp;</p>
<p>&nbsp;</p>
</div>

</div>
)
}
};
